.btn-to-top {
  z-index: 1;
  width: 50px;
  height: 50px;
  opacity: 0;
  visibility: hidden;
  cursor: pointer;
  color: orange;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 20%;
  justify-content: center;
  align-items: center;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  bottom: 3vh;
  right: -140px;
  box-shadow: 0 1px 2px rgba(60, 64, 67, .3), 0 2px 6px 2px rgba(60, 64, 67, .15);
}

.btn-to-top:hover {
  color: #fff;
  background-color: #000;
  animation: animate90 .4s linear .15s forwards;
}

.btn-to-top--visible {
  opacity: 1;
  visibility: visible;
  right: 2.5%;
}

.icon-to-top {
  width: 1.5em;
  height: 1.5em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  display: inline-block;
}

@keyframes animate90 {
  0% {
    transform: rotate(0deg);
  }

  50% {
    transform: scale(1.2)rotate(60deg);
  }

  100% {
    transform: scale(1.2)rotate(90deg);
  }
}

.btn-to-top--dark {
  background-color: orange;
}

.btn-to-top--dark:hover {
  color: purple;
}

